"use client";

import { useEffect, useState } from "react";
import { Select, SelectItem } from "@heroui/react";

type Kecamatan = {
  _id: string;
  kecamatan: string;
};

export default function KecamatanDropdown({ onSelect }: { onSelect: (id: string) => void }) {
  const [kecamatanList, setKecamatanList] = useState<Kecamatan[]>([]);
  const [selectedKecamatan, setSelectedKecamatan] = useState("");
  const colors = ["primary"];

  useEffect(() => {
    const fetchKecamatan = async () => {
      try {
        const response = await fetch("/api/kecamatan"); // Fetch data dari API
        const data = await response.json();
        setKecamatanList(data);
      } catch (error) {
        console.error("Gagal mengambil data kecamatan:", error);
      }
    };

    fetchKecamatan();
  }, []);

  const handleChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedId = event.target.value;
    setSelectedKecamatan(selectedId);
    onSelect(selectedId); // Kirim ID ke parent component
  };

  return (
    <div className="flex items-center gap-4">
    <h2 className="text-xl font-semibold">Detail Statistik Kecamatan</h2>
      {colors.map((color) => (
        <Select
          key={color}
          className="max-w-xs"
          color={ "danger"}
          value={selectedKecamatan || ""}
          label="Kecamatan"
          placeholder="Pilih PAC"
          onChange={(e) => setSelectedKecamatan(e.target.value)}
        >
          {kecamatanList.map((kec) => (
            <SelectItem key={kec._id}>{kec.kecamatan}</SelectItem>
          ))}
        </Select>
      ))}
    </div>
  );
}
